<template>
	<view>
		<input @blur="Written1" placeholder="手机号/用户名">
		<input @blur="Written2" placeholder="密码">
		<button @click="Load">登录</button> 
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone_number:'',
				password:''
			}
		},
		methods: {
			Written1:function(event){
				this.phone_number=event.target.value
			},
			Written2:function(event){
				this.password=event.target.value
			},
			Load:function(event){
				uni.request({
					url:'http://localhost:5000/load/load',
					data:{
						phone_number:this.phone_number,
						password:this.password
					},
					
					method:'POST',
					success:(res)=>{
						if(res.statusCode==200){
							uni.redirectTo({
								url:'/pages/user/user'
							})
						}
					}
				})
			}
		}
	}
</script>

